@import "mixins";

/*
*   Dash
*   A simple, lite-weight and powerful CSS framework
*   version: 1.0.0
*/

/* Unstyled element format. For example it can changes a link to look like as a normal text. */
/* Usage: -unstyled */
.#{$-prefix}unstyled,
.#{$-prefix}unstyled * {
  text-decoration: none;
  font-weight: normal;
  color: black;
  cursor: default;
  background: transparent;
  box-sizing: border-box;
  box-shadow: none;
  text-shadow: none;
  border: none;
  list-style: none;
  margin: 0;
  padding: 0;
  background-clip: border-box;
  scroll-behavior: smooth;
}

/* Link format */
/* Example: -link -link-hover -link-focus */
.#{$-prefix}link {
  @include states {
    text-decoration: none;
    cursor: pointer;
    color: $-pink;
    font-weight: bold;
    display: block;
  }
}

/* Bold text */
/* Example: -bold -bold-hover -bold-focus */
.#{$-prefix}t-bold {
  @include states {
    font-weight: bold;
  }
}

/* italic text */
/* Example: -italic -italic-hover -italic-focus */
.#{$-prefix}t-italic {
  @include states {
    font-style: italic;
  }
}

/* Text decoration formats */

/*** BEGIN ***/
/* Underlined text */
/* Example: -t-underline -t-underline-hover -t-underline-focus */
.#{$-prefix}t-underline {
  @include states {
    text-decoration: underline;
  }
}

.#{$-prefix}t-overline {
  @include states {
    text-decoration: overline;
  }
}

.#{$-prefix}t-line-through {
  @include states {
    text-decoration: line-through;
  }
}

.#{$-prefix}t-dashed {
  @include states {
    text-decoration: dashed;
  }
}

.#{$-prefix}t-dotted {
  @include states {
    text-decoration: dotted;
  }
}

.#{$-prefix}t-double {
  @include states {
    text-decoration: double;
  }
}

.#{$-prefix}t-wavy {
  @include states {
    text-decoration: wavy;
  }
}

.#{$-prefix}t-solid {
  @include states {
    text-decoration: solid;
  }
}

.#{$-prefix}t-none {
  @include states {
    text-decoration: none;
  }
}
/*** END ***/

/* Text transform */
/*** BEGIN ***/
.#{$-prefix}t-lowercase {
  @include states {
    text-transform: lowercase;
  }
}

.#{$-prefix}t-uppercase {
  @include states {
    text-transform: uppercase;
  }
}

.#{$-prefix}t-capitalize {
  @include states {
    text-transform: capitalize;
  }
}
/*** END ***/

/* Left to right direction  */
.#{$-prefix}ltr {
  direction: ltr;
}

/* Right to left direction  */
.#{$-prefix}rtl {
  direction: rtl;
}

/* Clear space from both sides of the element. */
.#{$-prefix}clear {
  clear: both;
}

/* Clear space from right side of the element. */
.#{$-prefix}clear-right {
  clear: right;
}

/* Clear space from left side of the element. */
.#{$-prefix}clear-left {
  clear: left;
}

/* Normal position */
.#{$-prefix}clear-none {
  clear: none;
}

/* Use `-clearfix` to clear space from both sides of the element. (cross browser support) */
/*** BEGIN ***/
.#{$-prefix}clearfix:before {
  content: "";
  display: table;
}

.#{$-prefix}clearfix:after {
  clear: both;
  content: ".";
  display: block;
  font-size: 0;
  height: 0;
  visibility: hidden;
}

.#{$-prefix}clearfix {
  zoom: 1;
  clear: both;
  width: 100%;
  min-height: 0.01px;
}
/*** END ***/

/* The box-sizing methods */
/*** BEGIN ***/
.#{$-prefix}box-content,
.#{$-prefix}box-content * {
  box-sizing: content-box;
}

.#{$-prefix}box-border,
.#{$-prefix}box-border * {
  box-sizing: border-box;
}
/*** END ***/

/* Object fit */
/*** BEGIN ***/
.#{$-prefix}fit-cover {
  object-fit: cover;
}

.#{$-prefix}fit-fill {
  object-fit: fill;
}

.#{$-prefix}fit-none {
  object-fit: none;
}

.#{$-prefix}fit-scale-down {
  object-fit: scale-down;
}
/*** END ***/

/* Aspect ratio */
/*** BEGIN ***/
.#{$-prefix}aspect-ratio {
  aspect-ratio: auto;
}

.#{$-prefix}aspect-video {
  aspect-ratio: 16 / 9;
}

.#{$-prefix}aspect-square {
  aspect-ratio: 1 / 1;
}

.#{$-prefix}aspect-2-1 {
  aspect-ratio: 2 / 1;
}

.#{$-prefix}aspect-3-2 {
  aspect-ratio: 3 / 2;
}

.#{$-prefix}aspect-4-3 {
  aspect-ratio: 4 / 3;
}

.#{$-prefix}aspect-34-21 {
  aspect-ratio: 34 / 21;
}

.#{$-prefix}aspect-1-2 {
  aspect-ratio: 1 / 2;
}

.#{$-prefix}aspect-2-3 {
  aspect-ratio: 2 / 3;
}

.#{$-prefix}aspect-3-4 {
  aspect-ratio: 3 / 4;
}

.#{$-prefix}aspect-21-34 {
  aspect-ratio: 21 / 34;
}
/*** END ***/

/* Position property */
/*** BEGIN ***/
.#{$-prefix}pos-absolute {
  position: absolute;
}

.#{$-prefix}pos-relative {
  position: relative;
}

.#{$-prefix}pos-static {
  position: static;
}

.#{$-prefix}pos-fixed {
  position: fixed;
}

.#{$-prefix}pos-sticky {
  position: sticky;
  top: 0;
}

.#{$-prefix}pos-0 {
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
}

.#{$-prefix}pos-center {
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  text-align: center;
}

.#{$-prefix}pos-10 {
  left: 10%;
  transform: translate(-90%, 0);
}

.#{$-prefix}pos-20 {
  left: 20%;
  transform: translate(-80%, 0);
}

.#{$-prefix}pos-30 {
  left: 30%;
  transform: translate(-70%, 0);
}

.#{$-prefix}pos-40 {
  left: 40%;
  transform: translate(-60%, 0);
}

.#{$-prefix}pos-50 {
  left: 50%;
  transform: translate(-50%, 0);
}

.#{$-prefix}pos-60 {
  left: 60%;
  transform: translate(-40%, 0);
}

.#{$-prefix}pos-70 {
  left: 70%;
  transform: translate(-30%, 0);
}

.#{$-prefix}pos-80 {
  left: 80%;
  transform: translate(-20%, 0);
}

.#{$-prefix}pos-90 {
  left: 90%;
  transform: translate(-10%, 0);
}

.#{$-prefix}pos-100 {
  left: 100%;
  transform: translate(0%, 0);
}

.#{$-prefix}pos-l {
  object-position: left;
}

.#{$-prefix}pos-r {
  object-position: right;
}

.#{$-prefix}pos-t {
  object-position: top;
}

.#{$-prefix}pos-b {
  object-position: bottom;
}

.#{$-prefix}pos-lt {
  object-position: left top;
}

.#{$-prefix}pos-rt {
  object-position: right top;
}

.#{$-prefix}pos-lb {
  object-position: left bottom;
}

.#{$-prefix}pos-rb {
  object-position: right bottom;
}

/*** END ***/

/* Inline element behavior */
.#{$-prefix}d-inline {
  display: inline;
}

/* Inline container behavior */
.#{$-prefix}d-inline-block {
  display: inline-block;
}

/* List item behavior */
.#{$-prefix}d-list-item {
  display: list-item;
}

/* Grid behavior */
.#{$-prefix}d-grid {
  display: grid;
}

/* Mouse cursor shapes */
/*** BEGIN ***/
/* Default mouse cursor */
.#{$-prefix}cursor-default {
  cursor: default;
}

/* Pointer mouse cursor */
.#{$-prefix}cursor-pointer {
  cursor: pointer;
}

.#{$-prefix}cursor-auto {
  cursor: auto;
}

.#{$-prefix}cursor-none {
  cursor: none;
}

.#{$-prefix}cursor-help {
  cursor: help;
}

.#{$-prefix}cursor-progress {
  cursor: progress;
}

.#{$-prefix}cursor-wait {
  cursor: wait;
}

.#{$-prefix}cursor-crosshair {
  cursor: crosshair;
}

.#{$-prefix}cursor-text {
  cursor: text;
}

.#{$-prefix}cursor-row-resize {
  cursor: row-resize;
}

.#{$-prefix}cursor-col-resize {
  cursor: col-resize;
}
/*** END ***/

/* Text formatters */
/*** BEGIN ***/
.#{$-prefix}t-break {
  page-break-before: always;
}

.#{$-prefix}t-nowrap {
  white-space: nowrap;
}

.#{$-prefix}t-ellipsis {
  text-overflow: ellipsis;
}

.#{$-prefix}t-single-row {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
/*** END ***/

/* Safe fonts*/
/*** BEGIN ***/
.#{$-prefix}font-sans-serif {
  font-family: sans-serif;
}

.#{$-prefix}font-arial {
  font-family: arial, /* Windows, MacOS */ helvetica,
    /* Unix+X, MacOS */ sans-serif;
}

.#{$-prefix}font-helvetica {
  font-family: arial, /* Windows, MacOS */ helvetica,
    /* Unix+X, MacOS */ sans-serif;
}

.#{$-prefix}font-verdana {
  font-family: verdana, /* Windows, MacOS */ /* Fallback options */ arial,
